.fixed-links {
	position: fixed;
	z-index: 10;
	right: 0;
	top: 25%;
	@include trs;
	@include mq($max-width: 1200px) {
		display: none; }
	// +mq($max-width: 1500px)
	// 	&.enter
	// 		transform: translate(60%, 0)
	// +mq($max-width: 1300px)
	// 	&.enter
	// 		transform: translate(80%, 0)
	&-link {
		width: 108px;
		height: 80px;
		background: $dark;
		@include flex(column, center, center, nowrap);
		text-align: center;
		color: white;
		text-transform: uppercase;
		@include mq($max-width: 1500px) {
			width: 80px;
			height: 60px; }
		&:hover {
			.fixed-links-link__text {
				color: $yellow; } }
		&:first-child {
			border-radius: 4px 0 0 0; }
		&:last-child {
			border-radius: 0 0 0 4px; }
		&__icon {
			display: block;
			height: 32px;
			@include mq($max-width: 1500px) {
				height: 20px; } }
		&__text {
			font: 800 12px/1 'Exo', sans-serif;
			margin: 6px 0 0 0;
			@include trs;
			width: 100%;
			@include mq($max-width: 1500px) {
				font-size: 8px; } } } }
