/* hamburger icon */
.main-menu-btn-icon,
.main-menu-btn-icon:before,
.main-menu-btn-icon:after {
  position: absolute;
  top: 50%;
  left: 2px;
  height: 2px;
  width: 24px;
  background: #555;
  -webkit-transition: all 0.25s;
  transition: all 0.25s; }

.main-menu-btn-icon:before {
  content: '';
  top: -7px;
  left: 0; }

.main-menu-btn-icon:after {
  content: '';
  top: 7px;
  left: 0; }

/* x icon */
